@import "variables";
@import "mixins";

.btn{
	height: @field-size;
	padding: 0 20px;
	color: @white;
	font-size: @fs-base;
	font-weight: 500;
	font-family: @font-family;
	vertical-align: middle;
	margin-bottom: @row-margin;
	white-space: nowrap;
	.border-radius();
	.transition();
	&[disabled]{
		background: @disabled!important;
		cursor: default;
	}
	& + &{margin-left: 20px;}
	&-sm{height: @sm-size;}
	&.addBtn{
		margin: 0 0 0 10px;
	}
	&_done{
		background: transparent!important;
		line-height: 30px;
	}
	&-transp-white{
		height: 38px;
		padding: 0 25px;
		font-size: 0.875rem;
		color: rgba(255,255,255,.7);
		background: transparent;
		border: 1px solid #fff;
		.transition();
		&:hover{color: rgba(255,255,255,11);}
	}
	&-transp-blue{
		height: 38px;
		line-height: 37px;
		padding: 0 25px;
		font-size: 0.875rem;
		color: #1a8ce7;
		background: transparent;
		border: 1px solid #1a8ce7;
		.transition();
		&:hover{opacity: 0.6;}
	}
	&-new{
		height: 36px;
		font-size: 0.875rem;
		padding: 0 25px;
		text-transform: uppercase;
	}
	&-show-map{
		margin-top: 9px;
		height: 36px;
		line-height: 36px;
		font-size: 0.875rem;
		padding: 0 25px;
		text-transform: uppercase;
	}

	&-logout{
		float: right;
		border-color: #cee3fb;
		margin: 13px 0 0;
	}
	&-createCompany{
		display: block;
		margin: 0 auto 50px;
	}
	&-ok,
	&-cancel{
		height: 55px;
		background: transparent;
		font-size: 0.875rem;
		text-transform: uppercase;
		font-weight: 500;
		padding: 0 10px;
		.transition(opacity, .2s, ease);
		&:hover,
		&:focus{opacity: .6;}
		&[disabled]{
			color: #a3a3a3;
			opacity: 1!important;
			cursor: default;
		}
	}
	&-ok{
		color: @brand-blue;
	}
	&-cancel{
		color: #000;
	}
	&-activate-blue{
		background: #00b0ff;
		padding: 0 18px;
		&:hover,
		&:focus{background: @brand-blue-hover;}
	}
	&-activate-green{
		background: #00bfa5;
		padding: 0 18px;
		margin-bottom: 6px;
		&:hover,
		&:focus{background: #03a891;}
	}
	&-upload-green{
		position: relative;
		float: right;
		width: 215px;
		padding: 0;
		text-align: center;
		margin-top: 26px;
		margin-left: 50px;
		-webkit-box-shadow: 1px 1px 6px rgba(0,0,0,0.24);
		-moz-box-shadow: 1px 1px 6px rgba(0,0,0,0.24);
		box-shadow: 1px 1px 6px rgba(0,0,0,0.24);

		&Icon{
			font-size: 11px;
			color: #fff;
			padding-right: 10px;
		}
		& > *{
			vertical-align: middle;
			position: relative;
			z-index: 1;
		}
		&.loading2{
			cursor: default;
			&:hover{
				background: @brand-green;
			}
		}
		&.loaded{
			cursor: default;
			&:hover{
				background: @brand-green;
			}
		}

	}
	&-del{
		margin: 24px 0 0;
	}
}
.red-btn{
	background: @brand-red;
	&.btn_done{color: @brand-red;}
	&:hover,
	&:focus{background: @brand-red-hover;}
}
.blue-btn{
	background: @brand-blue;
	&.btn_done{color: @brand-blue;}
	&:hover,
	&:focus{background: @brand-blue-hover;}
}
.green-btn{
	background: @brand-green;
	&.btn_done{color: @brand-green;}
	&:hover,
	&:focus{background: @brand-green-hover;}
}
.yellow-btn{
	background: @brand-yellow;
	&.btn_done{color: @brand-yellow;}
	&:hover,
	&:focus{background: @brand-yellow-hover;}
}


